import { Link } from "react-router-dom";
import Logotipo from "../../assets/log/doctors.svg";


import * as React from 'react';

import { Button, FormControl, Grid, IconButton, InputAdornment, InputLabel, MenuItem, OutlinedInput, Select, TextField, Tooltip } from "@mui/material";

import { VisibilityOffOutlined, VisibilityOutlined } from "@material-ui/icons";
import { ContactSupport } from "@mui/icons-material";




const Login = () => {
    

    const [typeID, setTypeId] = React.useState('');

    const handleChange = (event) => {
      setTypeId(event.target.value);
    };

    const [values, setValues] = React.useState({
      amount: '',
      password: '',
      weight: '',
      weightRange: '',
      showPassword: false,
    });
  
    const handleChange2 = (prop) => (event) => {
      setValues({ ...values, [prop]: event.target.value });
    };
  
    const handleClickShowPassword = () => {
      setValues({
        ...values,
        showPassword: !values.showPassword,
      });
    };
  
    const handleMouseDownPassword = (event) => {
      event.preventDefault();
    };

    return (
      
      
        <Grid container justifyContent="center">
          
          <Grid item xs={6} sm={6} mb={4} mt={2}>
              <img src={Logotipo} alt="logo"></img>
          </Grid>

          <Grid item xs={ 12 } sx={{ mt: 3 }}>
          <FormControl fullWidth>
            <InputLabel id="demo-simple-select-label">Tipo de Identificación</InputLabel>
            <Select
              required
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={typeID}
              label="Tipo de Identificación"
              onChange={handleChange}
            >
                <MenuItem value={1}>Cedula de Ciudadanía.</MenuItem>
                <MenuItem value={2}>Cedula de extranjería</MenuItem>
                <MenuItem value={3}>Menor sin identificación</MenuItem>
                <MenuItem value={4}>Adulto sin identificación</MenuItem>
                <MenuItem value={5}>Pasaporte</MenuItem>
                <MenuItem value={6}>Permiso especial de permanencia</MenuItem>
                <MenuItem value={7}>Registro civil</MenuItem>
                <MenuItem value={8}>Tarjeta de identidad</MenuItem>
                <MenuItem value={9}>Otro</MenuItem>
            </Select>
          </FormControl>
          </Grid>


          <Grid item xs={12} sx={{ mt: 2 }}>
              <TextField
                required
                id="numDocument"
                label="Número de Documento"
                name="numDocument"
                autoComplete="numDocument"
                fullWidth
              />
          </Grid>

          <Tooltip title="Ingrese año de nacimiento ó últimos 4 dígitos de tu celular" placement="right-start">
          <Grid item xs={ 12 } sx={{ mt: 2 }}>
          <FormControl variant="outlined" fullWidth>
          <InputLabel htmlFor="outlined-adornment-password">Codigo verificación</InputLabel>
          <OutlinedInput
            id="outlined-adornment-password"
            type={values.showPassword ? 'text' : 'password'}
            value={values.password}
            onChange={handleChange2('password')}
            endAdornment={
              <InputAdornment position="end">
                <IconButton
                  aria-label="toggle password visibility"
                  onClick={handleClickShowPassword}
                  onMouseDown={handleMouseDownPassword}
                  edge="end"
                >
                  {values.showPassword ? <VisibilityOutlined /> : <VisibilityOffOutlined />}
                </IconButton>
              </InputAdornment>
            }
            label="Codigo verificación"
            placeholder="Año de Nacimiento ó últimos 4 dígitos de tu celular"
            required
          />
          </FormControl>
          </Grid>
          </Tooltip>

          <Grid
              container
              justifyContent="center"
              spacing={2}
              sx={{ mb: 4, mt: 5 }}
            >
              <Grid item xs={12} sm={6}>
              <Link to="/home" style={{color: '#9C239C'}} activeStyle={{color: '#E3F2FD'}}>
                <Button variant="contained" fullWidth size="large" sx={{ borderRadius: 2, }}>
                  Ingresar
                </Button>
                </Link>
              </Grid>
          </Grid>

          <Grid container direction='row' justifyContent='center'>
            <Link to="/Support" style={{color: '#9C239C'}} activeStyle={{color: '#E3F2FD'}}>
              <Button startIcon={<ContactSupport titleAccess='Soporte'></ContactSupport>}>
                  {/*<ContactSupportOutlined titleAccess='Soporte'></ContactSupportOutlined>*/}
                <Link  to="/Support" style={{color: '#9C239C'}} activeStyle={{color: '#E3F2FD'}}>
                  Soporte técnico
                </Link>
              </Button>
            </Link>
          </Grid>

      </Grid>
      
    );
  }

  export default Login